import styled from "styled-components";
import React, { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";

const StyledImg = styled(motion.img)`
    max-width: 100%; /*  */
    max-height: 300px; /*  */
    object-fit: contain; /*  */
`;

const OpenACard = ({ items }) => {
    const [selectedId, setSelectedId] = useState(null);
    const selectedItem = items.find(item => item.id === selectedId);

    return (
        <>
            {items.map(item => (
                <motion.div
                    key={item.id}
                    layoutId={item.id}
                    onClick={() => setSelectedId(item.id)}
                    style={{
                        marginBottom: "10px",
                        padding: "10px",
                        border: "1px solid #ccc",
                        borderRadius: "5px",
                        cursor: "pointer",
                        backgroundColor: selectedId === item.id ? "#eee" : "white"
                    }}
                    whileHover={{ scale: 1.1 }}
                >
                    <motion.h5>{item.subtitle}</motion.h5>
                    <motion.h2>{item.title}</motion.h2>
                </motion.div>
            ))}

            <AnimatePresence>
                {selectedId && selectedItem && (
                    <motion.div
                        layoutId={selectedId}
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        style={{
                            position: "absolute",
                            top: 0,
                            left: 0,
                            right: 0,
                            bottom: 0,
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            background: "rgba(0, 0, 0, 0.8)",
                            color: "white"
                        }}
                    >
                        <motion.div
                            initial={{ scale: 0.5 }}
                            animate={{ scale: 1 }}
                            exit={{ scale: 0.5 }}
                            transition={{ duration: 0.5 }}
                        >
                            <motion.h3>{selectedItem.subtitle}</motion.h3>
                            <motion.h3>{selectedItem.subcontent}</motion.h3>
                            {selectedItem.subImg && (
                                <StyledImg src={selectedItem.subImg} alt={selectedItem.title} />
                            )}
                            <motion.h1>{selectedItem.title}</motion.h1>
                            <motion.button
                                onClick={() => setSelectedId(null)}
                                whileHover={{ scale: 1.1 }}
                                style={{
                                    padding: "8px 16px",
                                    backgroundColor: "#fe0222",
                                    color: "white",
                                    border: "none",
                                    borderRadius: "5px",
                                    cursor: "pointer"
                                }}
                            >
                                Close
                            </motion.button>
                        </motion.div>
                    </motion.div>
                )}
            </AnimatePresence>
        </>
    );
}

export default OpenACard;
