import React from "react";
import styled from 'styled-components';
import { Link } from "react-router-dom";

import DefaultLayout from "../components/layout/DefaultLayout";
import HomeBackground from "../components/common/HomeBackground";
import MaskDark from "../components/common/MaskDark";
import Title from "../components/common/Title";
import FontFamily from "../components/common/FontFamily";
import Content from "../components/common/Content";
import Middle from "../components/common/Middle";
import Title2 from "../components/common/Title2";
import Title2Content from "../components/common/Title2Content";

import homeBackground from "../images/logo.jpg";

const ContentSize = styled.div`
	width: 100%;
`;
// const Title2ContentImg = styled.img`
//     width: ${(props) => props.width || '100%'};
//     height: ${(props) => props.height || '600px'};
//     margin-top: 16px;
//     margin-left: 50px;
//     background-image: ${(props) => `url(${props.url})`};
//     background-position: center;
//     background-size: cover;
// `;
const Title2ContentVideo = styled.iframe`
    margin-top: 16px;
    margin-left: 180px;
    width: 100%;
    height: 450px;
    max-width: 600px;
    border: 1px solid #ccc;
    ${'' /* src: ${(props) => `url(${props.url})`}; */}
`;

const GalleryPage = () => {
    return (
        <DefaultLayout>
            <HomeBackground url={homeBackground}>
                <MaskDark>
                    <Middle>Gallery</Middle>
                </MaskDark>
            </HomeBackground>
            <ContentSize>
                <FontFamily>
                    <Title></Title>
                    <Content>
                        <Title2>(nyonyo)</Title2>
                        <Title2Content><Link to='https://github.com/typicframe/nyonyo'>(nyonyo)</Link></Title2Content>
                    </Content>
                    <Title></Title>
                    <Content>
                        <Title2></Title2>
                        <Title2Content>python</Title2Content>
                        <Title2ContentVideo src="https://www.youtube.com/embed/tl8ApwD0ud0?si=0074EqmLaWjFZrfU" title="YouTube video player" frameborder="0" 
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" 
                            allowfullscreen />
                    </Content>
                    <Title></Title>
                    <Content>
                        <Title2>APP</Title2>
                        <Title2Content></Title2Content>
                        <Title2ContentVideo src="https://www.youtube.com/embed/RoYatJwZVpk?si=J_WMvzd_qVPX95nf" title="YouTube video player" frameborder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin"
                            allowfullscreen />
                        <Title2>1A2B</Title2>
                        <Title2Content>python1A2B</Title2Content>
                        <Title2ContentVideo src="https://www.youtube.com/embed/7WtOEU1g7Jo?si=ndgv8jxOjRMnJhIK" title="YouTube video player" frameborder="0"
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin"
                            allowfullscreen />
                    </Content>
                    <Title></Title>
                    <Content>
                        <Title2>zhen</Title2>
                        <Title2Content>React </Title2Content>
                    </Content>
                </FontFamily>
            </ContentSize>
        </DefaultLayout>
    )
};

export default GalleryPage;